import * as actions from '../actionTypes';

const initialState = {
  total_hits: 0,
  list: []
}

export default (state = initialState, action) => {
  switch (action.type) {
    case actions.GET_LIST:
      return {
        ...state,
        list: [...action.payload.list],
        total_hits: action.payload.total_hits
      };
    default: return {...state};
  }
}