import './Header.scss';

import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faCheck, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';

import Modal from '../../components/Modal';
import * as validator from '../../utils/validationHelper';
import * as util from '../../utils/localStorageHelper';

export default class Header extends Component {
  state = {
    isShowing: false,
    url: '',
    selectedItem: {
      title: '',
      description: '',
      type: 'video',
      preview_image: '',
      video: '',
      hearted: false,
      date_created: ''
    }
  }

  validateUrl = (url) => {
    if (!url) {
      return false;
    }
    else if (!validator.validateUrl(url)) {
      return false;
    } else {
      return true;
    }
  }

  onChangeHandler = event => {
    event.persist();
    let nam = event.target.name;
    let val = event.target.value;

    const item = {
      ...this.state.selectedItem,
      [nam]: val
    }

    this.setState({
      ...this.state,
      selectedItem: item
    });
  }

  openModalHandler = (item = {}) => {
    this.setState({
      ...this.state,
      isShowing: true,
      selectedItem: item
    });

    document.body.style.overflow = "hidden";
  }

  closeModalHandler = () => {
    this.setState({
      ...this.state,
      isShowing: false
    });
    document.body.style.overflow = "auto";
  }

  onAddItem = () => {
    if (!this.state.selectedItem.preview_image || (!this.state.selectedItem.video && this.state.selectedItem.type === 'video')) {
      return;
    } else {
      util.setList(this.state.selectedItem);
      window.location.reload();
    }
  }

  render() {
    const { selectedItem } = this.state;
    return(
      <>
        <Modal
          className="modal"
          show={this.state.isShowing}
          close={this.closeModalHandler}>
            <div className="modal-header flex">
              <span className="modal-title">Add to collection</span>
              <span onClick={this.closeModalHandler} className="close">&times;</span>
            </div>
            <form>
              <div className="form-input">
                <input 
                  onFocus={this.onFocus} 
                  onBlur={this.onFocusOut} 
                  onChange={this.onChangeHandler}
                  value={selectedItem.title || ''}
                  type="text" 
                  name="title"
                  required/>
                <span className='place-holder'>Title</span>
              </div>
              <div className="form-input">
                <textarea 
                  onFocus={this.onFocus} 
                  onBlur={this.onFocusOut} 
                  value={selectedItem.description || ''}
                  onChange={this.onChangeHandler}
                  name="description"
                  required></textarea>
                <span className='place-holder'>Description</span>
              </div>
              <div className="form-input">
                <select name="type" value={selectedItem.type || ''} onChange={this.onChangeHandler}>
                  <option value='video'>Video</option>
                  <option value='image'>Image</option>
                </select>
                <span className="place-holder top">Type</span>
              </div>
              <div className="form-input">
                <input 
                  onFocus={this.onFocus} 
                  onBlur={this.onFocusOut}
                  onChange={this.onChangeHandler}
                  value={selectedItem.preview_image || ''}
                  type="text" 
                  name="preview_image"
                  required
                  />
                <span className='place-holder'>Link preview image url<span className="required">*</span></span>
                {
                  (!this.validateUrl(this.state.selectedItem.preview_image))
                    ? <p className="error">Link preview image is required</p>
                    : ''
                }
              </div>
              <div className="form-input">
                <input 
                onFocus={this.onFocus}
                onBlur={this.onFocusOut} 
                onChange={this.onChangeHandler}
                value={selectedItem.video || ''}
                type="text" 
                name="video"
                required/>
                <span className='place-holder'>Link file url<span className="required">*</span></span>
                {
                  (!this.validateUrl(this.state.selectedItem.video))
                    ? <p className="error">Link file url is required</p>
                    : ''
                }
              </div>
            </form>
            <div className="modal-footer">
              <button 
                onClick={this.onAddItem} 
                disabled={!this.state.selectedItem.video || !this.state.selectedItem.preview_image} 
                className="btn"><FontAwesomeIcon size="lg" icon={faCheck} />Add new item</button>
            </div>
        </Modal>
        <header>
          <h1>NASA Collection</h1>
          <div className="group-btn">
            <button onClick={this.openModalHandler} className="btn"><FontAwesomeIcon size="lg" icon={faPlus} />Add new item</button>
            <button onClick={this.openModalHandler} className="btn"><FontAwesomeIcon size="lg" icon={faArrowRight} />
              <Link className="" to="/search">To Search Page</Link>
            </button>
          </div>
        </header>
      </>
    );
  }
}
