import './Collection.scss';

import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';

import Item from './Item';
import VideoModal from '../VideoModal';
import Modal from '../Modal';
import * as util from '../../utils/localStorageHelper';
import * as validator from '../../utils/validationHelper';

export default class Collection extends Component {
  state = {
    isShowing: false,
    isShowingVideo: false,
    list: util.getList(),
    selectedIndex: null,
    selectedItem: {
      title: '',
      description: '',
      type: '',
      preview_image: '',
      video: '',
      hearted: false,
      date_created: ''
    }
  }

  openModalHandler = (item = {}, index) => {
    this.setState({
      ...this.state,
      isShowing: true,
      selectedItem: item,
      selectedIndex: index
    });

    document.body.style.overflow = "hidden";
  }

  closeModalHandler = () => {
    this.setState({
      ...this.state,
      isShowing: false
    });
    document.body.style.overflow = "auto";
  }

  onHeart = (index, hearted) => () => {
    const { list } = this.state;
    list[index].hearted = hearted;
    this.setState({
      ...this.state,
      list: list
    });

    util.updateList(this.state.list);
  }

  onDelete = (index) => () => {
    const { list } = this.state;
    list.splice(index, 1);
    this.setState({
      ...this.state,
      list: list
    });

    util.updateList(this.state.list);
  }

  onChangeHandler = event => {
    let nam = event.target.name;
    let val = event.target.value;

    const item = {
      ...this.state.selectedItem,
      [nam]: val
    }

    this.setState({
      ...this.state,
      selectedItem: item
    });
  }

  openVideoPlayer = (url = '') => () => {
    this.setState({
      ...this.state,
      isShowingVideo: true,
      url: url
    });
    document.body.style.overflow = "hidden";
  }

  closeVideoPlayer = () => {
    this.setState({
      ...this.state,
      isShowingVideo: false
    });
    this.refs.vidRef.pause();
    document.body.style.overflow = "auto";
  }

  validateUrl = (url) => {
    if (!url) {
      return false;
    }
    else if (!validator.validateUrl(url)) {
      return false;
    } else {
      return true;
    }
  }

  onEdit = () => {
    const { list } = this.state;
    list[this.state.selectedIndex] = this.state.selectedItem;
    this.setState({
      ...this.state,
      list: list
    });

    util.updateList(this.state.list); 
  }

  render() {
    const { list, selectedItem } = this.state;
    return(
      <>
        <ul className="collection">
          {
            list.map((item, key) => 
              <Item 
                item={item} 
                index={key} 
                key={key} 
                openModal={this.openModalHandler} 
                onHeart={this.onHeart}
                onDelete={this.onDelete}
                openVideo={this.openVideoPlayer}/>)
          }
        </ul>
        <VideoModal
        className="video-modal"
        show={this.state.isShowingVideo}
        close={this.closeModalHandler}>
          <div className="modal-header flex">
            <span className="modal-title">Edit</span>
            <span onClick={this.closeVideoPlayer} className="close">&times;</span>
          </div>
          <video
            ref="vidRef"
            controls
            src="http://images-assets.nasa.gov/video/NHQ_2017_0811_Preparing%20for%20Eclipse%202017%20on%20This%20Week%20@NASA%20%E2%80%93%20August%2011,%202017/NHQ_2017_0811_Preparing%20for%20Eclipse%202017%20on%20This%20Week%20@NASA%20%E2%80%93%20August%2011,%202017~large.mp4" type="video/mp4" 
          />
        </VideoModal>
        <Modal
          className="modal"
          show={this.state.isShowing}
          close={this.closeModalHandler}>
            <div className="modal-header flex">
              <span className="modal-title">Add to collection</span>
              <span onClick={this.closeModalHandler} className="close">&times;</span>
            </div>
            <form>
              <div className="form-input">
                <input 
                  onFocus={this.onFocus} 
                  onBlur={this.onFocusOut} 
                  onChange={this.onChangeHandler}
                  value={selectedItem.title}
                  type="text" 
                  name="title"
                  required/>
                <span className='place-holder'>Title</span>
              </div>
              <div className="form-input">
                <textarea 
                  onFocus={this.onFocus} 
                  onBlur={this.onFocusOut} 
                  value={selectedItem.description}
                  onChange={this.onChangeHandler}
                  name="description"
                  required></textarea>
                <span className='place-holder'>Description</span>
              </div>
              <div className="form-input">
                <select name="type" value={selectedItem.type} onChange={this.onChangeHandler}>
                  <option value='video'>Video</option>
                  <option value='image'>Image</option>
                </select>
                <span className="place-holder top">Type</span>
              </div>
              <div className="form-input">
                <input 
                  onFocus={this.onFocus} 
                  onBlur={this.onFocusOut}
                  onChange={this.onChangeHandler}
                  value={selectedItem.preview_image}
                  type="text" 
                  name="preview_image"
                  required
                  />
                <span className='place-holder'>Link preview image url<span className="required">*</span></span>
                {
                  (!this.validateUrl(this.state.selectedItem.preview_image))
                    ? <p className="error">Link preview image is required</p>
                    : ''
                }
              </div>
              {
                this.state.selectedItem.type === 'video'
                  ?
                  <div className="form-input">
                  <input 
                  onFocus={this.onFocus}
                  onBlur={this.onFocusOut} 
                  onChange={this.onChangeHandler}
                  value={selectedItem.video}
                  type="text" 
                  name="video"
                  required/>
                  <span className='place-holder'>Link file url<span className="required">*</span></span>
                  {
                    (!this.validateUrl(this.state.selectedItem.video))
                      ? <p className="error">Link file url is required</p>
                      : ''
                  }
                </div>
                  :
                ''
              }
            </form>
            <div className="modal-footer">
              <button 
                onClick={this.onEdit} 
                disabled={(!this.state.selectedItem.video && this.state.selectedItem.type === 'video') || !this.state.selectedItem.preview_image} 
                className="btn"><FontAwesomeIcon size="lg" icon={faCheck} />Save</button>
            </div>
        </Modal>
      </>
    );
  }
}
